import React from 'react';
import './label.css';

interface labelProps {
    /**
     * label 텍스트
     */
    text: string;

    /**
     * style 정의
     */
    style?: object;
}

export const Label = ({
    text,
    style,
    ...props
}) => {
    
    return <div style={style}>
        { text }
    </div>;
};